import Vue from 'vue'
import Router from 'vue-router'
//首页
import Home from '@/components/Home'

//消息
import Message from '@/components/message/Message'
//工作相关
import Work from '@/components/work/Work'
//添加客户
import AddClient from '@/components/work/AddClient'
//添加跟进
import AddFollowUp from '@/components/work/AddFollowUp'
//添加任务
import AddTask from '@/components/work/AddTask'
//添加订单
import AddOrder from '@/components/work/AddOrder'
//添加日志
import AddRecord from '@/components/work/AddRecord'
//添加项目
import AddProject from '@/components/work/AddProject'
//添加装修
import AddCase from '@/components/work/AddCase'
//添加工艺案例
import AddArtCase from '@/components/work/AddArtCase'
//添加设备
import AddDevice from '@/components/work/AddDevice'
//待办任务
import LoadTask from '@/components/work/LoadTask'
//添加设备
import RecordManage from '@/components/work/RecordManage'
//添加设备
import ScheduleRemind from '@/components/work/ScheduleRemind'

//我的客户
import ClientMe from '@/components/work/ClientMe'
//公共客户
import ClientPublic from '@/components/work/ClientPublic'
//历史客户
import ClientHistory from '@/components/work/ClientHistory'
//客户反馈
import ClientFeedback from '@/components/work/ClientFeedback'
//设备管理
import DeviceManage from '@/components/work/DeviceManage'
//项目管理
import ProjectManage from '@/components/work/ProjectManage'
//项目管理
import ProjectDesc from '@/components/work/ProjectDesc'

//我的设计
import MyDesign from '@/components/work/MyDesign'
//我的订单
import MyOrders from '@/components/work/MyOrders'
//我的工地
import MyWorkSite from '@/components/work/MyWorkSite'
//客户跟进
import ClientFollowUp from '@/components/work/ClientFollowUp'

//通讯录
import Contacts from '@/components/contacts/Contacts'
//通讯录详情
import ContactsInfo from '@/components/contacts/ContactsInfo'
//统计
import Statistics from '@/components/statistics/Statistics'
//其他
import Other from '@/components/other/Other'
//登录
import Login from '@/components/other/login'
//会员中心
import MemberCenter from '@/components/member/MemberCenter'
//账户中心
import AccountCenter from '@/components/account/AccountCenter'
Vue.use(Router)

 const router =new Router({
  routes: [
   {
      path: '/',
      name: 'Home',
      component: Home
  	},
   {
      path: '/Login',
      name: 'Login',
      component: Login
    },{
      path: '/Message',
      name: 'Message',
      component: Message
    },{
      path: '/Statistics',
      name: 'Statistics',
      component: Statistics
    },{
      path: '/Contacts',
      name: 'Contacts',
      component: Contacts
    },{
      path: '/Other',
      name: 'Other',
      component: Other
    },{
      path: '/ContactsInfo',
      name: 'ContactsInfo',
      component: ContactsInfo
    },{
      path: '/AddClient',
      name: 'AddClient',
      component: AddClient
    },{
      path: '/AddFollowUp',
      name: 'AddFollowUp',
      component: AddFollowUp
    },{
      path: '/AddTask',
      name: 'AddTask',
      component: AddTask
    },{
      path: '/AddOrder',
      name: 'AddOrder',
      component: AddOrder
    },{
      path: '/AddRecord',
      name: 'AddRecord',
      component: AddRecord
    },{
      path: '/AddProject',
      name: 'AddProject',
      component: AddProject 
    },{
      path: '/AddCase',
      name: 'AddCase',
      component: AddCase 
    },{
      path: '/AddArtCase',
      name: 'AddArtCase',
      component: AddArtCase 
    },{
      path: '/AddDevice',
      name: 'AddDevice',
      component: AddDevice
    },{
      path: '/LoadTask',
      name: 'LoadTask',
      component: LoadTask
    },{
      path: '/RecordManage',
      name: 'RecordManage',
      component: RecordManage 
    },{
      path: '/ScheduleRemind',
      name: 'ScheduleRemind',
      component: ScheduleRemind
    },{
      path: '/ClientMe',
      name: 'ClientMe',
      component: ClientMe
    },{
      path: '/ClientPublic',
      name: 'ClientPublic',
      component: ClientPublic
    },{
      path: '/ClientHistory',
      name: 'ClientHistory',
      component: ClientHistory
    },{
      path: '/ClientFeedback',
      name: 'ClientFeedback',
      component: ClientFeedback
    },{
      path: '/DeviceManage',
      name: 'DeviceManage',
      component: DeviceManage
    },{
      path: '/ProjectManage',
      name: 'ProjectManage',
      component: ProjectManage
    },{
      path: '/ProjectDesc',
      name: 'ProjectDesc',
      component: ProjectDesc
    },{
      path: '/MyDesign',
      name: 'MyDesign',
      component: MyDesign
    },{
      path: '/MyOrders',
      name: 'MyOrders',
      component: MyOrders
    },{
      path: '/MyWorkSite',
      name: 'MyWorkSite',
      component: MyWorkSite
    },{
      path: '/ClientFollowUp',
      name: 'ClientFollowUp',
      component: ClientFollowUp
    },{
      path: '/MemberCenter',
      name: 'MemberCenter',
      component: MemberCenter
    },{
      path: '/AccountCenter',
      name: 'AccountCenter',
      component: AccountCenter
    }
  ]
})
 export default router;



