
import AlertModule from 'vux/src/plugins/alert/module'

import wx from 'weixin-js-sdk';
function getCid() {
let cid = localStorage.getItem("cid");
if(isExist(cid)){
return cid;
}else{
return 0;
}
}

function getBid() {
var ubid=sessionStorage.getItem("ubid");
if(isExist(ubid)){
return ubid;
}else{
var ubid=onAuthorization();
sessionStorage.setItem("ubid",ubid);
return ubid;
}
}
function getUid() {
var localuid=localStorage.getItem("bid");
if(isExist(localuid)){
return localuid;
}else{
return 0;
}
}
function onAuthorization(){
var url = encodeURIComponent(window.location.href.split('#')[0]);
var uid=getnavivalues()["uid"];
if(!isExist(uid)){
window.location.href="https://www.homeeyes.cn/wx/Com_oauth?targetUrl="+url;
}else{
return uid;
}

}
function getPhone(){
let phone = localStorage.getItem("phone");

return phone;
}
function getnavivalues() {
var url = window.location.search; 
url=url.split("#/")[0];
var theRequest = new Array();
if (url.indexOf("?") !== -1) {
var str = url.substr(1);
var strs = str.split("&");
for (var i = 0; i < strs.length; i++) {
theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
}
}
return theRequest;
}

function toastAlert(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {

}
})

}

function onNextBack() {


document.addEventListener('plusready', function() {
var webview = plus.webview.currentWebview();
plus.key.addEventListener('backbutton', function() {
webview.canBack(function(e) {
if(e.canBack) {
webview.back();
} else {



var first = null;
plus.key.addEventListener('backbutton', function() {

if(!first) {
first = new Date().getTime();
console.log('再按一次退出应用');
setTimeout(function() {
first = null;
}, 1000);
} else {
if(new Date().getTime() - first < 1500) {
plus.runtime.quit();
}
}
}, false);
}
})
});
});

}

function showAlert(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {
console.log('Module: I\'m hiding now')
}
})

}

function getNowFormatDate(time) {
var date = new Date(time);
var seperator1 = "-";
var year = date.getFullYear();
var month = date.getMonth() + 1;
var strDate = date.getDate();
if(month >= 1 && month <= 9) {
month = "0" + month;
}
if(strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
var currentdate = year + seperator1 + month + seperator1 + strDate + " 00:00";
return currentdate;
}

function getTodayDate() {
var date = new Date();
var seperator1 = "-";
var seperator2 = ":";
var year = date.getFullYear();
var month = date.getMonth() + 1;
var strDate = date.getDate();
var hhstr = date.getHours();
var mmStr = date.getMinutes();
var ssStr = date.getSeconds();
if(month >= 1 && month <= 9) {
month = "0" + month;
}
if(strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
if(hhstr >= 0 && hhstr <= 9) {
hhstr = "0" + hhstr;
}
if(mmStr >= 0 && mmStr <= 9) {
mmStr = "0" + mmStr;
}
if(ssStr >= 0 && ssStr <= 9) {
ssStr = "0" + ssStr;
}
var currentdate = year + seperator1 + month + seperator1 + strDate + " " + hhstr + seperator2 + mmStr + seperator2 + ssStr;
return currentdate;
}

function isWinXin() {
var isWeiXin = navigator.userAgent.toLowerCase().indexOf('micromessenger') > -1 ? true : false;
return isWeiXin;
}
function isExist(value){
if(value!==null&&value!==undefined&&value.length>0&&value>0){
return true;
}else{
console.log(value);
return false;
}
}

export default {
getCid,
getUid,
showAlert,
getNowFormatDate,
isWinXin,
toastAlert,
getTodayDate,
onNextBack,
isExist,
getPhone,
getBid

}



