
import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VPoint from 'vux/src/components/v-chart/v-point.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VArea from 'vux/src/components/v-chart/v-area.vue'
import VLegend from 'vux/src/components/v-chart/v-legend.vue'
import VBar from 'vux/src/components/v-chart/v-bar.vue'
import VPie from 'vux/src/components/v-chart/v-pie.vue'

import appfooter from "../element/footer.vue"
const map = {
'施工项目': '40%',
'完成项目': '20%',
'新增项目': '18%'
}

export default {
name: 'page',
components: {
appfooter,
XHeader,
VChart,
VPoint,
VLine,
VScale,
VTooltip,
ButtonTab,
ButtonTabItem,
XProgress,
VArea,
VLegend,
VBar,
VPie
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "统计",
uid: 0,
cid: 0,
isShowHeader: true,
tabSelect: 0,
listdata: {},
companydata: {},
own: {},
chartdata: [{
time: '2016-08-06 00:00:00',
tem: 0
},
{
time: '2016-08-06 00:10:00',
tem: 0
},
{
time: '2016-08-07 00:30:00',
tem: 0
},
{
time: '2016-08-07 00:35:00',
tem: 0
},
{
time: '2016-08-08 01:00:00',
tem: 0
},
{
time: '2016-08-08 01:20:00',
tem: 0
},
{
time: '2016-08-9 01:40:00',
tem: 0
},
{
time: '2016-08-9 02:00:00',
tem: 0
},
{
time: '2016-08-10 02:20:00',
tem: 0
}
],
legendOptions: {
position: 'right',
itemFormatter(val) {
return val + '  ' + map[val]
}
},
yOptions: {
formatter(val) {
return val * 100 + '%'
}
},
map,
percentdata: [{
name: '施工项目',
percent: 0.4,
a: '1'
},
{
name: '完成项目',
percent: 0.2,
a: '1'
},
{
name: '新增项目',
percent: 0.18,
a: '1'
}
],
dataCamera: [{
"mac": "C51397016",
"alivetime": 172,
"name": "黑珍珠7-1802"
}],
dataUsers: [{
"uid": 325,
"alivetime": 149,
"name": "（悦江台）王莲"
}],
monthlyreport: {}
}
},
created() {
this.isShowHeader = !this.util.isWinXin();
var uid = this.util.getUid();
var cid = this.util.getCid();
if(this.util.isExist(uid) && this.util.isExist(cid)) {
this.uid = uid;
this.cid = cid;
this.onCompanyRecord();
}
},
methods: { //初始化事件
consoleIndex() {
if(this.tabSelect == 0) {
this.onCompanyRecord();
} else if(this.tabSelect == 1) {
this.onMonthlyReport();
}
console.log('click demo01', this.tabSelect)
},
onCompanyRecord: function() {
let params = {
cid: this.cid,
uid: this.uid
}
this.$axios.post(this.umcons.serviceAddress.COMPANY_RECORD, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.listdata = data;
this.companydata = data.companydata;
this.own = data.own;
var alive = data.companydata.alive;
var list = new Array();
this.chartdata = [];
for(var i = 0, len = alive.length; i < len; i++) {

var item = {
time: alive[i].day,
tem: alive[i].count
}
list.push(item)
}
this.chartdata = list;
} else {
this.showAlert("加载失败");
}

}.bind(this));
},
onMonthlyReport: function() {
let params = {
cid: this.cid
}
this.$axios.post(this.umcons.serviceAddress.GET_MONTH_RECORD, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.dataCamera = data.camera;
this.dataUsers = data.users;
this.monthlyreport = data;















} else {
this.showAlert("加载失败");
}

}.bind(this));
}
}
}



