
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'

export default {
name: 'page',
components: {
XInput,
Group,
XButton,
Cell,
AlertModule,
Alert
},
data() {
return {
title: "工作台",
results: [],
bid: 0,
phone: 0,
code: 0,
codeMsg: "获取验证码"
}
},
created() {
var params=this.$route.params.bid;
console.log(params);
var bid = this.util.getBid();

if(this.util.isExist(bid)) {
this.bid = bid;
}
},
methods: { //初始化事件
onLogin: function() {

if(this.phone.length != 11) {
this.showAlert("请输入手机号");
return;
}
if(this.code.length < 4) {
this.showAlert("请输入验证码");
return;
}

let params = {
uid: this.bid,
phone: this.phone,
code: this.code
}

this.$axios.post(this.umcons.serviceAddress.COM_BIND_COUNT, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.umcons.userinfo = data;
console.log(data);
localStorage.setItem("cid", data.pid);
localStorage.setItem("bid", data.id);
localStorage.setItem("phone", data.username);
this.$router.push({
name: 'Home',
params: {
cid: data.id
}
});
} else {
this.showAlert("加载失败");
}

}.bind(this));

},
getCode: function() {
if(this.phone.length != 11) {
this.showAlert("请输入手机号");
return;
}
let params = {
phone: this.phone,
type: 2
}
var that=this;
console.log(params);
this.$axios.post(this.umcons.serviceAddress.SEND_MES, params).then(function(response) {
console.log(response);
var time = 60;

that.codeMsg = time + '秒';

var timer = setInterval(function() {
if(time > 0) {
time--;
that.codeMsg = time + '秒';
} else {

that.codeMsg = "点击获取";
clearInterval(timer);
}

}, 1000);
}.bind(this));
},
onPush: function(cid) {

},
showAlert: function(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {
console.log('Module: I\'m hiding now')
}
})

}
}
}



