export default {
	name: 'avatar',
	components: {

	},
	data() {
		return {

		}
	},
	created() {

	},
	props: {
		flowslist: Array
	},
	methods: {
		onAmendSchedule: function(index) {
			this.$emit('onschedule', index);
		},
		onStartTime: function(index) {
			this.$emit('timestart', index);
		},
		onEndTime: function(index) {
			this.$emit('timeend', index);
		},
		formateState: function(state) {
			if(state == 0) {
				return "点击开始"
			} else if(state == 1) {
				return "确认完成"
			} else {
				return "已经完成"
			}

		}

	}
}



