
import photo from '../../../static/userphoto.jpeg';
import upload from "../element/upload.vue"
import stepsbar from "../element/stepsBar.vue"
import maskbox from "../element/maskingBox.vue"
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'

import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import DatetimeView from 'vux/src/components/datetime-view/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
name: 'page',
directives: {
TransferDom
},
components: {
XHeader,
XButton,
Popup,
XTextarea,
Group,
Cell,
upload,
Toast,
stepsbar,
Datetime,
DatetimePlugin,
maskbox,
XDialog,
DatetimeView,
AlertModule
},
data() {
return {
title: "项目管理",
type: '1',
uid: 0,
proid: 0,
actionshow: false,
content: "",
actionflow: false,
selectflow: "",
fid: 1,
toastText: "",
toastshow: false,
selectTime: "",
visibility: false,
showimg:[],
isShowMask: false,
isAlterTime: false,
showToast: true,
isupdata: 0,
broadcast: [{
"id": 9,
"uid": 35,
"name": "申花壹号院5幢222室",
"z_uid": 2,
"starttime_d": 1537804800,
"image": "http://47.97.109.9/upload/images/74a597bb80881faccba156a23ea08f9f.jpg",
"project_us": "[\"3\",\"4\",\"6\"]",
"state": 0,
"type": 0,
"area": 120,
"leader_id": 14,
"staff_share": 0,
"company": "家视装饰有限公司",
"project_us_count": 4,
"leader": "销售1号",
"stage": "水电布线",
"speed": 20
}],
flow: [],
project: {
"id": 8,
"uid": 43,
"name": "尚品半岛6-802",
"z_uid": 15,
"image": "http://web.yuegames.com/upload/images/9d6c40f07aace6821f73b1d840e62282.jpg",
"project_us": [{
"name": "陈砚茫",
"phone": "13868849152",
"job": "设计师",
"avatar": "http://web.yuegames.com/upload/images/3d5aed77df6e7a12466463fc346ae411.png"
}],
"state": 2,
"type": 0,
"area": 180,
"style": null,
"leader_id": 16,
"cameras": null,
"headurl": "http://47.97.109.9/headurl/43.jpg"
},
uploadUrl: "https://www.homeeyes.cn/api/upload_broad_image",
imgUrl: {},
selectSchedule: {},
defaulttime: "",
updataparem: {
bid: 0,
pid: 0,
fid: 0,
uid: 0,
content: "",
image: []
}

}
},
created() {
this.proid = this.$route.params.proid;
this.uid = this.util.getUid();

if(this.util.isExist(this.uid)) {
this.initPro();
} else {
this.util.showAlert("uid为空");
}

},
methods: { //初始化事件
backpage: function() {
this.$router.go(-1);
console.log("调用成功")
},
initPro: function() {
let params = {
uid: this.uid,
pro_id: this.proid
}
console.log(params);
var that = this;
this.$axios.post(this.umcons.serviceAddress.PROJECT_FLOW, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.project = data.project;
this.broadcast = data.broadcast;
this.flow = data.flow;

if(data.flow.length > 0) {
this.selectflow = data.flow[0].name;
}

} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},
onUpdataBroadcast: function(index) {
this.isupdata = 1;
var data = this.broadcast[index];
this.updataparem.uid = this.uid;
this.updataparem.pid = this.proid;
this.updataparem.bid = data.id;
this.updataparem.fid = data.f_id;
this.updataparem.content = data.content;
this.updataparem.image = data.image;
this.actionshow = true;

},
addBroadcast: function(index) {
this.isupdata = 0;
this.updataparem.uid = this.uid;
this.updataparem.pid = this.proid;
this.updataparem.bid = 0;
this.updataparem.fid = 0;
this.updataparem.content = "";
this.updataparem.image = [];
this.actionshow = true;

},
onMaskClose: function(state) {
this.isShowMask = state;
},
clickImg: function(src) {

this.showimg = src;
this.isShowMask = true;
console.log(src);
},
onAddBroadcast: function() {
this.actionshow = false;
let params = {
pro_id: this.proid,
f_id: this.fid,
uid: this.uid,
content: this.content,
image: this.imgUrl
}
this.$axios.post(this.umcons.serviceAddress.CREATE_BROADCAST, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.util.showAlert(response.data.mes);
} else {
this.util.showAlert("加载失败");
}

}.bind(this));
},
onDelBroadcast: function(id) {
var that = this;
AlertModule.show({
title: '提示',
content: "是否删除播报信息？",
onShow() {
console.log('显示')
},
onHide() {
let params = {
bid: id
}
that.$axios.post(that.umcons.serviceAddress.DEL_BROADCAST, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
that.util.showAlert(response.data.mes);
that.initPro();
} else {
that.util.showAlert("加载失败");
}

}.bind(that));
}
})

},
onEvent(event) {
console.log('on', event)
}, //选择时间
change(nowtime) {
if(nowtime != undefined && nowtime.length > 0) {
this.visibility = false;
var flow = this.selectSchedule;
if(flow.state >= 0) {
var newstate = flow.state;
if(!this.isAlterTime) {
newstate = flow.state + 1;
}

let params = {
pro_id: flow.pro_id,
f_id: flow.id,
state: newstate,
time: nowtime
}
console.log('plugin confirm', params)
this.$axios.post(this.umcons.serviceAddress.EDIT_FLOW_STATE, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.util.showAlert(response.data.mes);
this.toastText = "修改成功";
this.toastshow = true;
console.log(response.data.mes);
setTimeout(this.initPro(), 500);
} else {
if(response.data.mes != undefined) {
this.toastText = response.data.mes;
} else {
this.toastText = "修改失败";
}

this.toastshow = true;
}

}.bind(this));
}
} else {
this.util.showAlert("请选择时间");
}

},
onChange(index) {
this.selectflow = this.flow[index].name;
this.fid = this.flow[index].id;
this.actionflow = false;

},
formateState: function(state) {
if(state == 0) {
return "点击开始"
} else if(state == 1) {
return "确认完成"
} else {
return "已经完成"
}

},
imageCall: function(data) {
this.imgUrl = data;
this.actionshow = false
setTimeout(this.initPro(), 500);

},
getLenght: function(array) {
if(array.length > 0) {
return true;
} else {
return false;
}
console.log("图片：" + array)
},
showPlugin: function(index) {
var nowflow = this.flow[index];
this.selectSchedule = nowflow;
this.defaulttime = this.util.getTodayDate();
this.visibility = true;
this.isAlterTime = false;
},
selectStartTime: function(index) {
var nowflow = this.flow[index];
if(nowflow.state > 1) {
this.selectSchedule = nowflow;
this.defaulttime = nowflow.starttime;
console.log(nowflow.starttime);
this.visibility = true;
this.isAlterTime = true;
}

},
selectEndTime: function(index) {
var nowflow = this.flow[index];
if(nowflow.state == 2) {
this.selectSchedule = nowflow;
this.defaulttime = nowflow.endtime;
console.log(nowflow.endtime);
this.visibility = true;
this.isAlterTime = true;
}
}
}
}



