
import photo from '../../../static/userphoto.jpeg';
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'page',
components: {
XHeader
},
data() {
return {
title: "公共客户",
type: '1',
defaultImg: 'this.src="' + require('../../assets/userphoto.jpeg') + '"',
prolist: [],
uid: 0
}
},
created() {
this.uid = this.util.getUid();
if(this.uid == null || this.uid == undefined) {
this.uid = 33;
}
this.onLoadPro();
},
methods: { //初始化事件
onLoadPro: function() {
let params = {
uid: this.uid
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GET_CUSTOMER, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.prolist = response.data.data;

} else {
this.showAlert("加载失败");
}

}.bind(this));
},
getInfo: function(msg) {
if(msg != undefined) {
return msg;
} else {
return "暂无数据"
}
},
getPhoto: function(msg) {
if(msg != undefined) {
return msg;
} else {
return photo;
}
}
}
}



