
import photo from '../../../static/userphoto.jpeg';
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Search from 'vux/src/components/search/index.vue'

const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}
export default {
name: 'page',
components: {
XHeader,
Scroller,
Search
},
data() {
return {
title: "项目管理",
type: '1',
uid: 0,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
prolist: [{

}],
isPullup: true,
isPulldown: true,
value: ''
}
},
created() {

this.uid = this.util.getUid();
if(this.util.isExist(this.uid)) {
this.initPro("");
}



},
methods: {
backpage:function(){
this.$router.go(-1);
console.log("调用成功")
},

initPro: function(condition) {

let params = {
uid: this.uid,
condition: condition
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GETMYPROJECT, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.prolist = data;
} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},
openProjectFlow: function(index) {
console.log(index);
var data = this.prolist[index];
this.$router.push({
name: 'ProjectDesc',
params: {
proid: data.id
}
});
},
pulldownLoading() {
console.log("下拉刷新");
var that = this;
setTimeout(function() {
that.initPro();
that.$refs.mScroller.donePulldown();
}, 1000);

},
pullupLoading() {

if(this.isPullup) {
this.isPullup = false;
var that = this;
that.$refs.mScroller.donePullup()
that.$refs.mScroller.disablePullup();
console.log("上拉加载")
setTimeout(function() {
that.$refs.mScroller.enablePullup();
that.isPullup = true;
}, 1000);
}

},
onSubmit() {
this.$refs.search.setBlur()
/*this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})*/
},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
},
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult(val) {
console.log('on-change', val);
if(val != undefined && val.length > 0) {
setTimeout(this.initPro(val), 500);

}
},

formatDate: function(time) {
time=parseInt(time+"000");
return this.util.getNowFormatDate(time);
}
}
}

function getResult(val) {
let rs = []
for(let i = 0; i < 5; i++) {
rs.push({
title: "项目" + i,
other: i
})
}
return rs
}



