
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import AlertModule from 'vux/src/plugins/alert/module'


export default {
name: 'page',
components: {
XHeader,
Group,
Cell,
AlertModule
},
data() {
return {
title: "会员中心",
isShowHeader: true,
userinfo: {
name:"程女士",
companyname:"你当前还不是会员",
job:"会员说明",
avatar:"https://www.homeeyes.cn/app/images/icon_company.png"
}
}
},
created() {
this.isShowHeader = !this.util.isWinXin();


},
methods: { //初始化事件
initUserInfo(uid, pid) {

let params = {
uid: uid,
pid: pid
}

this.$axios.post(this.umcons.serviceAddress.GET_USERINFO, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.userinfo = data;
if(this.util.isExist(data.id)) {
localStorage.setItem("bid", data.id)
}
if(this.util.isExist(data.cid)) {
localStorage.setItem("cid", data.cid)
}
if(this.util.isExist(data.username)) {
localStorage.setItem("phone", data.username)

} else {
this.$router.push({
name: 'Login',
params: {
bid: data.id
}
});
}
} else {
this.showAlert("加载失败");
}

}.bind(this));
},
goToPage: function(page) {
this.$router.push({
name: page,
params: {
cid: '2'
}
});
}
}
}



