
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import AlertModule from 'vux/src/plugins/alert/module'

import appfooter from "./element/footer.vue"
import Personitem from "./element/PersonItem.vue"
import Projectitem from "./element/ProjectItem.vue"
export default {
name: 'page',
components: {
appfooter,
XHeader,
Group,
Search,
Cell,
Grid,
GridItem,
GroupTitle,
TransferDom,
AlertModule,
Personitem,
Projectitem
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "智家视",
newUser: [],
project: [],
value: '',
cid: 2,
uid: 0,
isShowHeader: true
}
},
created() {
var uid = this.util.getUid();
var cid = this.util.getCid();
var bid=this.util.getBid();
if(!this.util.isExist(uid)) {
this.initUserInfo(bid,cid);
} else if(!this.util.isExist(cid)) {
this.initUserInfo(bid,cid);
} else {
this.uid = uid;
this.cid = cid;
this.initInfo();
}

this.isShowHeader = !this.util.isWinXin();

},
methods: { //初始化事件
initInfo(){
let params = {
uid: this.uid,
cid: this.cid
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.COM_MP_IDNEX, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.newUser=data.newUser;
this.project=data.project;
} else {
this.showAlert("加载失败");
}

}.bind(this));
},initUserInfo(uid,cid){

let params = {
uid: uid,
pid: cid
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GET_USERINFO, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.uid = data.id;
this.cid = data.cid;
localStorage.setItem("bid",data.id);
localStorage.setItem("cid",data.cid);
this.initInfo();
if(this.util.isExist(data.username)){
localStorage.setItem("phone",data.username)
}
} else {
this.showAlert("加载失败");
}

}.bind(this));
},
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
},
goToPage: function(page) {
this.$router.push({
name: page,
params: {
cid: '2'
}
});
},
openProjectFlow: function(index) {
console.log(index);
var data = this.project[index];
this.$router.push({
name: 'ProjectDesc',
params: {
proid: data.id
}
});
},
openClientFlow: function(id) {
this.$router.push({
name: 'AddClient',
params: {
uid: id
}
});
}
}

}

function getResult(val) {
let rs = []
for(let i = 0; i < 5; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}



