export default {
	name: 'masking',
	data() {
		return {
			showimg: "",
			showindex: 0,
			baseimgs: [],
			isend: false
		}
	},
	created() {
		//初始化显示状态
		this.isshow = false;
	},
	//接受父对象传递数据
	props: {
		imgs: Array,
		isshow: false
	},
	watch: {
		//监听图片数组
		imgs: {
			handler: function(val, oldval) {
				if(val != oldval) {
					this.$nextTick(() => {
						if(this.isEmptry(this.imgs)) {
							//更新数组
							this.baseimgs = this.imgs;
							//初始化下标
							this.showindex = 0;
							//初始化隐藏最后一张图提示
							this.isend=false;
							//更新设置图片
							this.onUpdataImg();
						}
					})
				}
			},
			immediate: true, //关键
			deep: true
		},
	},
	methods: {
		//通知父组件关闭
		commitExit() {
			//关闭方法为onmaskclose
			this.$emit('onmaskclose', false);
		}, //左移
		onLeft: function() {
			this.showindex--;
			if(this.showindex > 0) {
				this.onUpdataImg();
			} else {
				this.showindex = 0;
				this.onUpdataImg();
			}
			//关闭最后一页提示
			if(this.isend) {
				this.isend = false;
			}

		}, //右移
		onRight: function() {
			this.showindex++;
			if(this.showindex < this.baseimgs.length) {
				this.onUpdataImg();
			} else {
				//提示最后一张图
				this.isend = true;
				//设置当前下标为数组有效下标最大值
				this.showindex = this.baseimgs.length - 1;
				//设置为最后一张图
				this.onUpdataImg();
			}

		}, //判断数组是否为空
		isEmptry: function(value) {
			if(value != undefined && value.length > 0) {
				return true;
			} else {
				return false;
			}
		},//更新设置图片
		onUpdataImg: function() {
			//判断数组
			if(this.isEmptry(this.baseimgs)) {
				this.showimg = this.baseimgs[this.showindex];
			}else{
				this.isshow = false;
				this.commitExit();
			}
		}
	}
}



