
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import selectimage from "../element/selectImage.vue"

export default {
name: 'page',
directives: {
TransferDom
},
components: {
XHeader,
Group,
XInput,
XButton,
PopupPicker,
Datetime,
Cell,
XTextarea,
XNumber,
Checker,
CheckerItem,
XSwitch,
Popup,
selectimage,
XDialog,
ChinaAddressV4Data,
XAddress,
ButtonTab,
ButtonTabItem
},
data() {
return {
title: "新增案例",
username: "",
stylize: "",
type: 1,
gotourl: "",
month: 0,
area: 0,
selectAddress: "",
value: [],
addressData: ChinaAddressV4Data,
showAddress: false,
detailedaddress: "",
childtab: 0,
showfullview: false,
sharestaff: false,
showToast: false,
actionStaffs: false,
selectType: false,
images: [],
fullimages: [],
cid: 0,
staffs: [],
residence: [],
leadername: "请选择作者",
leader_id: 1,
uid: 0,
rid: 0,
ridname: "请选择楼盘",
house: "",
showRidPopup: false,
case_id: 0,
caseinfo: {
"id": 3,
"title": "原木系北欧风",
"uid": 4,
"photo": "http://47.97.109.9/upload/anli/bb08fcb700b16f44a8e061c3700309c6.jpg",
"panorama": [
"http://47.97.109.9/upload/anli/e4ecc426bfb05c877d7a1340905b3a53.jpg",
"http://47.97.109.9/upload/anli/a128ef2ffc75bf966beafe3e5947c8aa.jpg",
"http://47.97.109.9/upload/anli/3c7c6ada8bdb7bd865257962202e80c9.jpg"
],
"area": 82,
"style": "美式风格",
"url": "http://47.97.109.9/HomeEye/study2.html",
"addtime": 1533865599,
"address": "杭州",
"is_up": 1,
"cid": 2,
"is_appup": 0,
"sort": 0,
"rid": null,
"hot": 0,
"type": 0,
"price": "0.00",
"author": "大海"
}

}
},
created() {
this.case_id = this.$route.params.id;
this.cid = this.util.getCid();
if(this.cid == null || this.cid == undefined) {
this.cid = 2;
}
this.caseinfo.cid = this.cid;
this.initSettingInfo();
if(this.case_id != undefined) {
setTimeout(this.initPro(), 500);
}

},
methods: {
/**
* 初始化设置信息
*/
initSettingInfo: function() {
let params = {
cid: this.cid
}
this.$axios.post(this.umcons.serviceAddress.CASE_SOURCE, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;

this.staffs = data.uids;
this.residence = data.residence;
console.log(data);
} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},
/**
* 初始化案例信息
*/
initPro: function() {
let params = {
case_id: this.case_id
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.CASE_DETAIL, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.caseinfo = data;
if(data.author!=null&&data.author!=undefined){
this.leadername = data.author;
}

this.selectAddress=data.address;
if(data.rid != undefined && data.rid != null && this.residence != undefined && this.residence.length > 0) {
var index = this.residence.indexOf(data.rid);
if(index != -1) {
this.ridname = this.residence[index];
}

}

console.log(data);
} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},

onAddFollowUp: function() {
this.caseinfo.address = this.selectAddress + this.detailedaddress;
if(this.sharestaff) {
this.caseinfo.is_up = 1;
} else {
this.caseinfo.is_up = 0;
}
let params = {
case_id: this.case_id,
title: this.caseinfo.title,
uid: this.caseinfo.uid,
cid: this.caseinfo.cid,
style: this.caseinfo.style,
type: this.caseinfo.type,
area: this.caseinfo.area,
photo: this.caseinfo.photo,
rid: this.caseinfo.rid,
url: this.caseinfo.url,
address: this.caseinfo.address,

is_up: this.caseinfo.is_up
}

console.log(params)
this.$axios.post(this.umcons.serviceAddress.CREATE_CASE, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.mes;
this.util.showAlert(data);

} else {
this.util.showAlert("加载失败");
}

}.bind(this));
},getImageSize:function(){
if(this.caseinfo!=undefined&&this.caseinfo.panorama!=undefined){
if(this.caseinfo.panorama.length>0){
return true;
}else{
return false;
}
}else{
return false;
}

},
logHide(str) {
console.log('on-hide', str)
},
logShow(str) {
console.log('on-show')
},
consoleIndex() {
console.log('click demo01', this.childtab)
},
onShadowChange(ids, names) {

console.log(names);
this.selectAddress = names[0] + names[1] + names[2];
},
onEvent(event) {
console.log('on', event);
},

change(value) {
console.log('change', value);
},
change(val, label) {
console.log('change', val, label)
},
selectRid: function(index, name) {
this.caseinfo.rid = index;
this.ridname = name;
this.showRidPopup = false;
},
imageCall: function(data) {
if(this.selectType) {
this.fullimages = data;
this.caseinfo.panorama = data;
} else {
this.images = data;
this.caseinfo.photo = data[0];
}

this.showToast = false;
console.log(data)
},
selectImage: function(type) {
this.selectType = type;
this.showToast = true;
},
onSelectPrincipal: function(id, namme) {
this.caseinfo.uid = id;
this.leadername = namme;
this.actionStaffs = false;
}
}
}



