
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import selectimage from "../element/selectImage.vue"
import appfooter from "../element/footer.vue"
export default {
name: 'page',
directives: {
TransferDom
},
components: {
appfooter,
XHeader,
Group,
XInput,
XButton,
PopupPicker,
Datetime,
Cell,
XTextarea,
XNumber,
Checker,
CheckerItem,
Divider,
XSwitch,
Checklist,
Popup,
selectimage,
XDialog
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "创建项目",
username: "",
userphone: "",
type:1 ,
remind: "",
userphone: "",
month: 0,
area: 0,
shareclient: false,
sharestaff: false,
actionshow: false,
showToast: false,
actionUser: false,
actionStaffs: false,
showFlows: false,
minuteListValue: '2018-10-01 09:00',
textdesc: "",
decorateType: "请选择流程模板",
decorateStates: [
['需求收集', '需求分析', '设计中', '开始施工', '水电工程']
],
decorateState: ['需求收集'],
images: [],
varmax: 1,
uploadUrl: "",
params: {},
objectList: [{
key: '1',
value: '001 value'
}, {
key: '2',
value: '002 value'
}, {
key: '3',
value: '003 value'
}],
objectListValue: [],
images: [],
cid: 0,
users: [],
staffs: [],
leadername:"请选择负责人",
staffscount:"请选择项目成员",
leader_id: 1,
uid: 0,
flows: [],
seekeruser: []

}
},
created() {
this.minuteListValue = this.util.getNowFormatDate();
this.cid = this.util.getCid();
if(this.cid == null || this.cid == undefined) {
this.cid = 2;
}
this.initPro();
},
methods: {
initPro: function() {
let params = {
cid: this.cid
}
this.$axios.post(this.umcons.serviceAddress.GET_PROJECT_DATA, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.users = data.users;
this.staffs = data.staff;
this.flows = data.flow;

} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},

onAddFollowUp: function() {
let params = {
name: this.username,
uid: this.uid,
starttime_d: this.minuteListValue,
cid: this.cid,
project_us: this.objectListValue,
type: this.type,
area: this.area,
month: this.month,
leader_id: this.leader_id,
temp: this.decorateType,
image: this.images
}
console.log(params)
this.$axios.post(this.umcons.serviceAddress.CREAT_PROJECT, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.mes;
this.util.showAlert(data);

} else {
this.util.showAlert("加载失败");
}

}.bind(this));
},
change(value) {
console.log('change', value);
},
onShow() {
console.log('on show')
},
onHide(type) {
console.log('on hide', type)
},
onChange(val) {
console.log('val change', val)
},
previewMethod() {

},
addImageMethod() {

},
removeImageMethod() {

},changestaffs(data){
this.staffscount=data.length+"人";
},
change(val, label) {
console.log('change', val, label)
},
seeker(val, label) {
if(val.length < 11 && val.length > 2) {
var users = this.seekeruser;
if(users.length <= 0) {
users = this.users;
} else {
this.actionUser = false;
}
if(users.length > 0) {
var newusers = new Array();
for(var index = 0; index < users.length; index++) {
var phone=users[index].phone;
if(phone.search(val) != -1) {
newusers[newusers.length] = users[index];

} else {
console.log(phone)
}
}
console.log(newusers)
if(newusers.length > 0) {
this.actionUser = true;
this.seekeruser = newusers;
} else {
this.actionUser = false;
this.seekeruser = [];
}
} else {
this.actionUser = false;
}
} else {
this.actionUser = false;
this.seekeruser =[];
}

},
onStaffFinish: function() {
this.actionshow = false;
console.log(this.objectListValue);
},
imageCall: function(data) {
this.images = data;
this.showToast = false;
console.log(data)
},
onSelectPrincipal: function(id) {
var staffs=this.staffs[id];
this.leader_id = staffs.id;
this.leadername=staffs.name;
this.actionStaffs = false;
},
onSelectStaff: function(id) {
if(this.isInArray(this.objectListValue, id)) {
this.objectListValue.push(id);
}
console.log(this.objectListValue);
},
isInArray: function(arr, value) {
if(arr.indexOf && typeof(arr.indexOf) == 'function') {
var index = arr.indexOf(value);
if(index >= 0) {
return true;
}
}
return false;
},
onSelectUser: function(index) {
var users=this.seekeruser[index];
this.userphone = users.phone;
this.uid=users.id;
this.actionUser = false;
}
}
}



