
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import selectimage from "../element/selectImage.vue"

export default {
name: 'page',
directives: {
TransferDom
},
components: {
XHeader,
Group,
XInput,
XButton,
Cell,
Popup,
selectimage,
XDialog
},
data() {
return {
title: "新增工艺",
username: "",
showToast: false,
actionStaffs: false,
selectType: false,
images: [],
cid: 0,
staffs: [],
leadername: "请选择作者",
uids: []


}
},
created() {

this.cid = this.util.getCid();
if(this.cid == null || this.cid == undefined) {
this.cid = 2;
}
this.initPro();
},
methods: {
initPro: function() {
let params = {
cid: this.cid
}
this.$axios.post(this.umcons.serviceAddress.CASE_SOURCE, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;

this.staffs = data.uids;

console.log(data);
} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},

onAddFollowUp: function() {
let params = {
name: this.username,
uids: this.uids,
cid: this.cid,
image: this.images
}
console.log(params)
this.$axios.post(this.umcons.serviceAddress.CREATE_ART, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.mes;
this.util.showAlert(data);

} else {
this.util.showAlert("加载失败");
}

}.bind(this));
},

imageCall: function(data) {
this.images = data;
this.showToast = false;
console.log(data)
},
selectImage: function(type) {
this.selectType = type;
this.showToast = true;
},
onSelectPrincipal: function(id, namme) {
var index=this.uids.indexOf(id);
if(index==-1){
this.uids.push(id);
}else{
this.uids.splice(index,1);
}
this.leadername = this.uids.length+"人";
console.log(this.uids)
}
}
}



