
import photo from '../../../static/userphoto.jpeg';
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'


export default {
name: 'page',
components: {
XHeader,
Group,
CellFormPreview,
Cell,
AlertPlugin
},
data() {
return {
title: "设备管理",
type: '1',
defaultImg:'this.src="' + require('../../assets/live_base.jpg') + '"',
prolist: [{
"id": 26,
"name": "10",
"mac": "C51396792",
"code": null,
"uid": 0,
"addtime": "2018-09-29 19:31:52",
"cid": 2,
"pro_id": 0,
"isSupportPTZ": 1,
"isSupportTalk": 1,
"isSupportZoom": 1,
"begintime": null,
"endtime": null,
"picUrl": "https://statics.ys7.com/device/assets/imgs/public/companyDevice.jpeg",
"picUrltime": 0,
"money": "0.00",
"staff_share": 0,
"user_share": 0,
"hls": null,
"rtmp": null,
"status": 0,
"defence": 0,
"isEncrypt": 0,
"alarmSoundMode": 0,
"offlineNotify": 1,
"videoLevel": 2,
"cameraNo": 1,
"isShared": "0"
}],
uid: 0
}
},
created() {
this.uid = this.util.getUid();

if(this.uid == null || this.uid == undefined) {
this.uid = 33;
}
this.onLoadPro();
document.getElementById("")
},
methods: { //初始化事件
onLoadPro: function() {
let params = {
uid: this.uid
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GET_YS, params).then(function(response) {
var error = response.data.error;
if(error == 0) {


this.prolist = response.data.data;

} else {
this.showAlert("加载失败");
}

}.bind(this));
},
getItem: function(itemData) {
var item = {
title: '学林冬院-办公室',
time: "直播中",
list: [{
label: '设备绑定：',
value: '张三丰'
}, {
label: '序列号：',
value: 'C2213123'
}, {
label: '添加时间：',
value: '2018-08-11'
}]
};
item.title = itemData.name;
if(itemData.status == 1) {
item.time = "在线";
} else {
item.time = "离线";
}

item.list[0].value = itemData.mac;
item.list[1].value = itemData.id;
item.list[2].value = itemData.addtime;
return item;
},openLive:function(index){
var device=this.prolist[index];
var mac=device.mac;
var cid = 1000+device.cid;;
var live="https://web.yuegames.com/app/livedemo/liveplay.html?mac="+mac+"&invitation="+cid;
console.log(live);
window.location.href =live;
},
showAlert: function(msg) {

this.$vux.alert.show({
title: '提醒',
content: msg,
onShow() {
console.log('Plugin: I\'m showing')
},
onHide() {
console.log('Plugin: I\'m hiding')
}
})
}
}
}



