},addDevice: function() {
let params = {
uid:this.uid,
alias: this.alias,
serial:this.serial,
verification:this.verification
}
this.$axios.post(this.umcons.serviceAddress.STUDENT_HOME, params).then(function(response) {
this.pageinfo = response.data.result;
console.log(response.data.result);
}.bind(this));
}
}
}



