
import photo from '../../../static/userphoto.jpeg';
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'page',
components: {
XHeader,

},
data() {
return {
title: "我的客户",
type: '1',
defaultImg: 'this.src="' + require('../../assets/userphoto.jpeg') + '"',
prolist: [{
title: '沙悟净',
time: "重要客户",
list: [{
label: '客户状态：',
value: '初步达成意向'
}, {
label: '负责人：',
value: '王经理'
}, {
label: '成交总额：',
value: '￥20W'
}, {
label: '最近联系：',
value: '2018-08-11'
}]
}],
uid: 0
}
},
created() {

this.uid = this.util.getUid();
if(this.util.isExist(this.uid)) {
this.onLoadPro();
}else{
this.uid = this.util.getBid();
this.onLoadPro();
}

},
methods: { //初始化事件
onLoadPro: function() {
let params = {
uid: this.uid
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GET_CUSTOMER, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.prolist = response.data.data;

} else {
if(this.util.isExist(response.data.mes)){
this.util.showAlert(response.data.mes);
}else{
this.util.showAlert("加载失败");
}

}

}.bind(this));
},
getInfo: function(msg) {
if(msg != undefined) {
return msg;
} else {
return "暂无数据"
}
},
getPhoto: function(msg) {
if(msg != undefined) {
return msg;
} else {
return photo;
}
},
openClientFlow: function(id) {
this.$router.push({
name: 'AddClient',
params: {
uid: id
}
});
}
}
}



