
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import appfooter from "../element/footer.vue"
import gril from '../../../static/gril.png';
import boy from '../../../static/boy.png';
export default {
name: 'page',
components: {
appfooter,
XHeader,
Group,
Panel,
AlertModule,
Alert,
Confirm
},
directives: {
TransferDom
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "通讯录",
list: [],
letterarray: [],
cid: 0,
isShowHeader: true,
isConfirm: false,
confirmMsg: "你确定要拨打电话？"
}
},
created() {
this.cid = this.util.getCid();
if(this.util.isExist(this.cid)) {
this.onLoadStaff();
var letterarray = new Array();
for(var i = 0; i < 26; i++) {
letterarray[i] = String.fromCharCode(65 + i);
}
this.letterarray = letterarray;
this.isShowHeader = !this.util.isWinXin();
}

},
methods: { //初始化事件
onImgError(item, $event) {
console.log(item, $event)
},
letterClick: function(arg) {
console.log(arg);
},
onLoadStaff: function() {
let params = {
cid: this.cid
}
this.$axios.post(this.umcons.serviceAddress.GET_STAFF, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.list = data;

} else {
this.showAlert("加载失败");
}

}.bind(this));
},
getItem: function(itemData) {
var item = {
src: boy,
title: '沙和尚',
desc: '18815575555',
url: '',
};
item.title = itemData.name;
item.desc = itemData.username;
return item;
},
showAlert: function(msg) {
AlertModule.show({
title: '提示',
content: msg,
onShow() {
console.log('Module: I\'m showing')
},
onHide() {
console.log('Module: I\'m hiding now')
}
})

},
callPhone: function(index) {
var phone=this.list[index].username;
if(this.isPhone(phone)) {
this.confirmMsg="你确定要拨打电话："+phone;
this.isConfirm = true;

}

},
toTransform: function(img) {
if(img.indexOf("https") != -1) {
return img;
} else {
return img.replace("http", "https");
}

},
onHide() {
console.log('on hide')
},
onShow() {
console.log('on show')
},
onCancel() {
console.log('on cancel')
},
onConfirm(phone) {
console.log('on confirm')
if(this.isPhone(phone)) {

location.href = "tel:" + phone;
}
this.isConfirm = false;
},
isPhone: function(value) {
if(this.util.isExist(value)) {
if(value.length == 11) {
return true;

} else {
return false;
}
} else {
return false;
}
}

}
}



